
#portfolio-navbar {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: justify;
      justify-content: space-between;
  background: #181818;
  width: 60px;
  height: 100%;
  position: absolute;
  top: 0;
  z-index: 3;
  overflow-y: auto;
}

@media (max-width: 960px) {
  #portfolio-navbar {
    width: 100%;
    height: 60px;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 3;
    min-height: 0;
  }
}

.burger-menu-icon {
  display: none;
}

@media (max-width: 530px) {
  #portfolio-navbar {
    position: fixed;
    overflow-y: initial;
  }
  .hide-navigation-icons {
    display: none;
  }
  .burger-menu-icon {
    display: initial;
    float: right;
    /* visibility: visible; */
  }
  .home-and-burger-container {
    height: 100%;
  }
}

@media (max-height: 400px) {
  #portfolio-navbar {
    -ms-flex-pack: start;
        justify-content: flex-start;
  }
}






